* {
  background-color: rgb(235, 233, 233);
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
}

.container-header {
  align-items: center;
  background-color: rgb(44, 44, 44);
  display: flex;
  justify-content: center;
  height: 70px;
}

.title-header {
  background-color: rgb(44, 44, 44);
  color: white;
}

.container-search {
  background-color: rgb(235, 233, 233);
  display: flex;
  justify-content: space-around;
}

.link-search {
  align-self: center;
  background-color: gray;
  border: solid 1px;
  border-radius: 5px;
  color: white;
  text-decoration: none;
  padding: 4px 10px;
}

.container-input-genre-favorite {
  display: flex;
  justify-content: space-around;
  width: 300px;
}

.input-container {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  width: 600px;
}

.input-search, .select-search {
  background-color: white;
  border-radius: 5px;
  font-size: 15px;
  padding: 2px;
}

.input-search {
  height: 20px;
  padding-left: 10px;
}

.label-favorite {
  align-self: center;
}

.loading {
  color: rgb(44, 44, 44);
  font-size: 40px;
  padding: 40px;
}

.container-movies {
  background-color: rgb(235, 233, 233);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.container-cardMovie {
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 450px;
  width: 300px;
  margin: 10px;
}

.container-info-card {
  background-color: white;
  display: flex;
  flex-direction: column;
  height: 350px;
  justify-content: space-between;
  padding: 10px;
}

.container-title-card {
  background-color: white;
  display: flex;
  justify-content: space-between;
}

.img-card {
  height: 170px;
}

.title-card {
  background-color: white;
}

.subtitle-card {
  background-color: white;
  color: gray;
}

.storyline-card {
  background-color: white;
  height: 120px;
}

.rating-card {
  text-align: center;
  background-color: gray;
  color: white;
  padding: 3px 0;
}

.link-details {
  background-color: white;
  color: blue;
  text-decoration: none;
  align-self: center;
}

.container-add-movie {
  display: flex;
  justify-content: center;
}

.container-form-add {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  padding: 30px 20px;
  width: 360px;
}

.subtitle-add-movie {
  align-self: center;
  font-size: 25px;
  padding: 10px 0;
}

.container-inputs {
  display: flex;
}

.container-btn-form {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.input-add {
  border: solid 1px;
  background-color: white;
  border-radius: 5px;
  margin-right: 20px;
  padding: 2px 10px;
}

.image-add {
  width: 340px;
}

.ratio-add {
  width: 60px;
}

.genre-add {
  height: 22px;
  margin-right: 60px;
}

.container-btn {
  align-self: center;
}

.btn-form {
  background-color: #343a40;
  border-radius: 5px;
  color: white;
  padding: 2px 15px;
  margin: 20px 0;
  width: 100px;
}

.label-add-movie {
  display: block;
  margin-top: 10px;
}

.container-btns-details {
  background-color: white;
  display: flex;
  justify-content: space-around;
}

.container-card-details {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  width: 100%;
}

.card-details {
  flex-direction: row;
  height: 370px;
  width: 80%;
}

.img-details {
  height: 370px;
}

.btn-details {
  width: 80px;
}

.info-details {
  width: 40%;
}

@media screen and (max-width: 500px) {
   .title-header {
     font-size: 25px;
   }

   .link-search {
     margin-top: 10px;
   }

  .container-search {
    flex-direction: column;
    width: auto;
  }

  .input-container {
    align-items: center;
    flex-direction: column;
    width: auto;
  }

  .container-input-genre-favorite {
    margin-top: 10px;
  }

  .card-details {
    align-items: center;
    flex-direction: column;
    width: 320px;
    height: 460px;
  }

  .container-cardMovie {
    width: 320px;
  }

  .img-details {
    height: auto;
    width: 320px;
  }

  .info-details {
    width: 270px;
  }
  .container-add-movie {
    justify-content: center;
    width: 100%;
  }

  .container-form-add {
    width: 270px;
    padding: 5px;
  }

  .subtitle-add-movie {
    font-size: 20px;
  }

  .titles-form {
    flex-direction: column;
  }

  .input-textArea {
    width: 250px;
  }

  .image-add {
    width: 250px;
  }

  .genre-input {
    align-items: center;
  }
}

@media screen and (min-width: 501px) and (max-width: 900px) {
  .container-search {
    flex-direction: column;
  }

  .input-container {
    justify-content: space-around;
    width: auto;
  }

  .link-search {
    margin-top: 10px;
  }

  .card-details {
    align-items: center;
    flex-direction: column;
    width: 500px;
    height: auto;
  }

  .img-details {
    height: auto;
    width: 500px;
  }

  .info-details {
    width: 400px;
    height: 230px;
  }
}
